<template>
    <div class="box-style invitation-list-box">
        <div class="gradient-label-box">
            <label class="gradient-label">{{ $t('home.invitationList') }}</label>
        </div>
        <div class="recommend-box">
            <div class="recommend-info-box">
                <div class="recommend-info-item">
                    <div class="recommend-info">{{ state.userInfo.recommends }}</div>
                    <div class="recommend-info-title">{{ $t('invitationList.recommendNumber') }}</div>
                </div>
                <div class="recommend-info-item">
                    <div class="recommend-info">{{ state.userInfo.dynamics }}</div>
                    <div class="recommend-info-title">{{ $t('invitationList.recommendAward') }}/HYC</div>
                </div>
            </div>
        </div>
        <div class="list-box">
            <div class="list-title">
                <label>{{ $t('invitationList.address') }}</label>
                <label>{{ $t('invitationList.power') }}</label>
            </div>
            <div class="list" v-for="item in state.invitationList" :key="item.address">
                <label>{{ $filters.addressFormat(item.address) }}</label>
                <label>{{ item.power }} T</label>
            </div>
        </div>
    </div>
</template>

<script setup>

import { reactive, onMounted, watch } from 'vue';
import { useRoute } from 'vue-router';
import { getUser, recommendRecord } from '@/utils/api';
import { Toast } from 'vant';
import axios from 'axios';

const route = useRoute()
const state = reactive({
    userInfo: {},
    invitationList: []
})

watch(() => route.account, () => {
    getData()
})
onMounted(async () => {
    getData()
})
const getData = (async () => {
    Toast.loading()
    axios.all([
        await getUser({ address: route.account }),
        await recommendRecord({ address: route.account }),
    ]).then(axios.spread(function (user, record) {
        state.userInfo = user.data
        state.invitationList = record.data
    }))
})

</script>

